import { Box, Button, Flex, Link, useColorMode } from "@chakra-ui/react";
import React from "react";
import { MoonIcon, SunIcon, HamburgerIcon } from "@chakra-ui/icons";

const Navbar = () => {
  const { colorMode, toggleColorMode } = useColorMode();
  return (
    <Box id="nav-menu" pos={"sticky"} top="0">
      <Flex
        justify={"space-around"}
        height="9vh"
        align={"center"}
        border="2px solid"
        transition={"all 1s"}
      >
        <Box>
          <Link href="#">yakshith</Link>
        </Box>
        <Flex width={"50rem"} justify="space-around" align={"center"}>
          <Link className="nav-link home">home</Link>
          <Link href="#about" className="nav-link about">
            about
          </Link>
          <Link className="nav-link skills">skills</Link>
          <Link className="nav-link projects">projects</Link>
          <Link className="nav-link contact">contact</Link>
          <Button className="nav-link resume">resume</Button>
          <Button onClick={toggleColorMode}>
            {colorMode === "light" ? <MoonIcon /> : <SunIcon />}
          </Button>
        </Flex>
        {/* <Box>
          <Button>
            <HamburgerIcon />
          </Button>
        </Box> */}
      </Flex>
    </Box>
  );
};

export default Navbar;
